@import url(https://fonts.googleapis.com/css2?family=Crimson+Pro:ital,wght@0,200..900;1,700);
/* styling scroolbar */

 ::-webkit-scrollbar{
   width: 5px;
   height: 6px;
 }

 ::-webkit-scrollbar-track{
   box-shadow: inset 0 0 5px #a5aaad;
   border-radius: 10px;
 }

 ::-webkit-slider-thumb{
   background: #3ea175;
   border-radius: 10px;
 }

 ::-webkit-scrollbar-thumb:hover{
    background: #a5aaad;
 }

 /* general styling */

 *{
   margin: 0px;
   padding:0px ;
 }

 body{
   box-sizing: border-box;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   
   };

   .text-primary-p{
     color: #a5aaad;
     font-size: 14px;
     font-weight: 700;
   }

   .font-bold{
     font-weight: 700;
   }

   .text-title{
     color:#2e4a66;
   }
   .text-lightblue{
     color:#469cac;
   }

   .text-red{
     color: #cc3d38;
   }

   .text-yellow{
     color: #a98921;
   }

   .container{
     display: grid;
     height: 100vh;
     grid-template-columns: 0.8fr 1fr 1fr 1fr;
     grid-template-rows: 0.2fr 3fr ;
     grid-template-areas:
     "sidebe nav nav nav"
     "sidebar main main main";
   }

   @media only screen and(max-width:978px) {
     .container{
       grid-template-columns: 1fr;
       grid-template-rows: 0.2fr 3fr;
       grid-template-areas: 
       "nav"
       "main";
     }
     
   }

 
#sidebar{
    background: skyblue;
    grid-area: sidebar;
    overflow-y: auto;
    padding: 20px;
    transition: all 0.5s;
    border-radius: 20px;
}

.sidebar_title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    margin-bottom: 90px;
}
.sidebar_img{
    display: flex;
    align-items: center;
    border-radius: 20px;

}

.sidebar_title > div >img{
    width: 75px;
    height: 30px;
    border-radius: 30px;
    object-fit: contain;
}

.sidebar_title > div > h1{
    font-size: 18px;
    display: inline;
}

.sidebar_title >i{
    font-size: 18px;
    display: none;
}
.sidebar_link{
    color: white;
    padding: 18px;
    border-radius: 3px;
    margin-bottom: 5px;
}


.sidebar_link > a{
    color: white;
    text-decoration: none;
    font-weight: 700;
}
.sidebar_link > i{
    margin-right: 15px;
    font-size: 18px;
}
.sidebar_logout>button{
    margin-top: 20px;
    padding: 10px;
    color: tomato;
}
.sidebar_logout >button> a{
    color: white;
    font-size: 15px;
    text-decoration: none;
    font-weight: 700;
}
.sidebar_logout >button> i{
    margin-right: 15px;
    font-size: 25px;
}
#button{
    padding:7px ;
    border-radius: 15px;
    background-color: rgb(21, 150, 209);
}



.sidebar_responsive{
    display: inline!important;
    z-index: 999 !important;
    left: 0 !important;
    position: absolute;
}

@media only screen and(max-width:978px){
    #sidebar{
        display: none;
    }

    .sidebar_title > i{
        display: inline;

    }
}
